<script>
import { list } from './index-magazine-list.js'
import { lastest, qiye } from './index-news.js'
import CweaFooter from '@/views/layout/components/cwea-footer.vue'
import IndexNav from './index-nav.vue'
import './index-fullpage.scss'

export default {
  name: 'FullPageIndex',
  data () {
    return {
      showHeader: false,
      list,
      lastest,
      qiye,
      magazineFocus: 1,
      //  0
      leftEnd: 0,
      options: {
        licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        afterLoad: this.afterLoad,
        onLeave: this.onLeave,
        navigation: false,
        navigationPosition: 'right',
        anchors: ['page1', 'page2', 'page3'],
        /*
         *  sectionsColor: [
         *  '#41b883',
         *  '#ff5f45',
         *  '#0798ec',
         *  '#fec401',
         *  '#1bcee6',
         *  '#ee1a59',
         *  '#2c3e4f',
         *  '#ba5be9',
         *  '#b4b8ab'
         *  ],
         */
        loopBottom: true,
        // navigationTooltips
        navigationTooltips: ['page1', 'page2', 'page3', 'page4', 'page5'],
        // showActiveTooltiptooltipfalse
        showActiveTooltip: true,
        // slidesNavigation false
        slidesNavigation: true,
        // slidesNavPositionbottomtopbottom
        slidesNavPositon: 'top',
        // scrollOverflowfalse,truejquery.slimscroll

        // sectionSelector:section.section

        // slideSelector:slide.slide
        slideSelector: '.slide',

        controlArrows: true,
        scrollBar: true,
      }
    }
  },
  components: {
    CweaFooter,
    IndexNav
  },
  computed: {
    magazineLength () {
      return this.list.length
    }
  },
  methods: {
    afterLoad: function (origin, destination, direction) {
      if (destination.anchor === 'page1') {
        this.showHeader = false
      } else {
        this.showHeader = true
      }
    },
    onLeave: function (origin, destination, direction) {
      // console.log('leave', origin.index)
      this.showHeader = false
    },
    buildFullPage () {
      // :skip-init="true" 
      this.$refs.fullpage.init()
    },
    destroyFullPage () {
      this.$refs.fullpage.destroy()
    },
    testClick (str) {
      window.alert(str)
    },
    warn (item, index) {
      console.log(index, item)
      this.magazineFocus = index
    },
    move (num) {
      // +120 -120 num  1  -11 -1 

      //  (length - 4)*120*(-1) ~ (length - 4)*120
      if (num > 0 && this.leftEnd < 0) {
        console.log('', this.leftEnd, num)
        this.leftEnd = this.leftEnd + 120
      } else if (num < 0 && this.leftEnd > (4 - this.magazineLength) * 120) {
        this.leftEnd = this.leftEnd - 120
      } else {
        this.leftEnd = 0
      }
    }
  },
  activated () {
    // this.buildFullPage()
    console.log('')
  },
  deactivated () {
    // slide  bug 

    // this.destroyFullPage()
    console.log('')
  },
}
</script>

